<script>
  import common from '../libs/common';
  import server from '../config/api';
  import Vue from 'vue';
  import 'cropper';
  import 'cropper/dist/cropper.js';
  import 'cropper/dist/cropper.css';

  export default {
    computed: {
      activeNaviMenu: function(){
        return this.$store.state.active_navi_menu;
      },
      platformLogoImg: function() {
        return (this.$store.state.current_platform.platformLogo === undefined
        || this.$store.state.current_platform.platformLogo === null
        || this.$store.state.current_platform.platformLogo.length === 0) ? this.defaultWordLogo
          : this.$store.state.current_platform.platformLogo;
      },
      platformLogoName: function() {
        return '消防安全管家';
      }
    },
    data() {
      return {
        common: common,
        highestLevel: undefined,
        currentLevel: undefined,
        userInfoStatus: 'baseInfo',
        editAvatarModal: false,
        editUserName: false,
        imgFile: undefined,
        trueVerifyCode: undefined,
        verifyCode: undefined,
        verifyCodeLoading: false,
        verifyCodeContent: undefined,
        waitTime: undefined,
        countDownId: undefined,
        oldTel: undefined,
        newTel: undefined,
        newUserName: undefined,
        newPassword: undefined,
        confirmNewPassword: undefined,
        user:{
          name: '测试用户',
          monitorCenter: {
            name:''
          }
        },
        defaultWordLogo: require('../assets/index/word_logo.png'),
        imgType: undefined,
        imgName: undefined,
      }
    },
    methods:{
      dropdownChanged: function(visible) {
        if(!visible) {
          setTimeout(() => {
            this.userInfoStatus = 'baseInfo';
          }, 200);
          this.oldTel = undefined;
          this.newTel = undefined;
          this.newUserName = undefined;
          this.newPassword = undefined;
          this.confirmNewPassword = undefined;
        }
      },
      onNaviMenuClick: function(key){
        // 清空返回列表
        this.clearYunSession('route_list');
        let page = {
          path: "/"+key
        }
        if(key === 'company') {
          this.$root.eventHub.$emit('setOrgInfo', null);
          page.query = {
            active_name: 'base_info'
          }
        }
        // 点击menu跳转
        this.$router.push(page);
      },
      backToWelcome: function() {
        delete localStorage.current_org_id;
        delete localStorage.current_org_Name;
        this.$router.push("/welcome");
      },
      back: function() {
//        this.$router.push("/welcome");
        window.history.go(-1);
      },
      onUserDropdownClick(clickMenuItem){
        if(clickMenuItem == 'mi_person_info'){
          this.editUserInfoModal = true;
          this.isEdit = false;
          this.isEditPassword = false;
          this.oldPassword = undefined;
        }else if(clickMenuItem == 'mi_logout'){
          this.logout();
        }
      },
      logout: function(){
        this.$store.commit('logout');
        localStorage.clear();
        sessionStorage.clear();
        this.$router.push({name:'login'});
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
//                this.$Message.success('提交成功!');
            this.$http.put(server.api.user.put.replace('{userId}', this.user.id), this.user).then((response)=>{
              this.$Message.success('修改成功!');
              // 删除用户密码
              this.user.password = undefined;
              this.$store.commit("setUser", this.user);
              this.editUserInfoModal = false;
            },(error)=>{
              this.$Message.error('修改失败!');
            });
          } else {
            this.$Message.error('修改失败!');
          }
        })
      },
      clickUploadButton: function() {
        $("#upload").click();
      },
      showUserEdit: function() {
        this.newUserName = this.user.name;
        this.editUserName = true;
      },
      changeUserName: function() {
        if(this.newUserName === null || this.newUserName === undefined || this.newUserName.length === 0) {
          this.$Message.error("用户名不能为空！");
          return;
        }
        this.$http.put(server.api.user.update, {
          'uid': localStorage.access_user_id,
          'name': this.newUserName
        }).then((response) => {
          this.$Message.success("修改用户名成功！");
          this.editUserName = false;
          let user = this.$store.state.user;
          user.name = this.newUserName;
          this.$store.commit("setUser", user);
          this.user.name = this.newUserName;
        }, (error) => {
          this.$Message.error("修改用户名失败！");
        });
      },
      countDown: function() {
        this.verifyCodeContent =  --this.waitTime + '秒';
        if(this.waitTime === 0) {
          window.clearInterval(this.countDownId);
          this.verifyCodeLoading = false;
        }
      },
      getVerifyCode: function() {
        // 获取验证码
        if(this.userInfoStatus === 'editPhone1' || this.userInfoStatus === 'editPassword1') {
          // 验证原手机号
          let user = this.$store.state.user;
          if(this.oldTel !== user.tel) {
            this.$Message.error("原手机号错误！");
            return;
          }
          this.waitTime = 60;
          this.verifyCodeContent =  this.waitTime + '秒';
          this.countDownId = setInterval(this.countDown, 1000);
          this.verifyCodeLoading = true;
          this.$http.get(server.api.verifyCode + "?tel=" + this.oldTel).then((response) => {
            this.trueVerifyCode =  response.body.data.verifyCode;
            console.log(this.trueVerifyCode);
          }, (error) => {
            this.$Message.error("获取验证码失败！");
          });
        } else {
          // 验证新手机号
          this.$http.get(server.api.verifyTel + "?tel=" + this.newTel).then((response) => {
            if(!response.body.meta.success) {
              this.$Message.error("手机号校验失败！");
              return;
            }
            if(response.body.data) {
              this.$Message.error('该手机号已注册！');
              return;
            }
            this.waitTime = 60;
            this.verifyCodeContent =  this.waitTime + '秒';
            this.countDownId = setInterval(this.countDown, 1000);
            this.verifyCodeLoading = true;
            this.$http.get(server.api.verifyCode + "?tel=" + this.newTel).then((response) => {
              this.trueVerifyCode =  response.body.data.verifyCode;
              console.log(this.trueVerifyCode);
            }, (error) => {
              this.$Message.error("获取验证码失败！");
            });
          }, (error) => {
            this.$Message.error("校验手机号出错！");
          });
        }
      },
      userInfoCancle: function () {
        this.userInfoStatus = 'baseInfo';
        this.oldTel = undefined;
        this.newTel = undefined;
        this.newUserName = undefined;
        this.newPassword = undefined;
        this.confirmNewPassword = undefined;
      },
      stepOne: function() {
        // 修改绑定个手机，验证原手机
        if(this.trueVerifyCode === undefined) {
          this.$Message.error("请获取验证码！");
          return;
        }
        if(this.verifyCode !== this.trueVerifyCode) {
          this.$Message.error("验证码错误！");
          return;
        }
        this.verifyCodeLoading = false;
        this.verifyCode = undefined;
        this.trueVerifyCode = undefined;
        this.userInfoStatus = 'editPhone2';
      },
      stepTwo: function() {
        // 修改绑定手机号，验证新手机
        if(this.verifyCode !== this.trueVerifyCode) {
          this.$Message.error("验证码错误！");
          return;
        }
        this.trueVerifyCode = undefined;
        this.$http.put(server.api.user.update, {
          'uid': localStorage.access_user_id,
          'tel': this.newTel
        }).then((response) => {
          this.$Message.success("修改用户手机号成功！需要重新登录");
          setTimeout(() => {
            this.logout();
          }, 1000);
        }, (error) => {
          this.$Message.error("修改用户手机号失败！");
        });
      },
      pwStepOne: function() {
        // 修改绑定个手机，验证原手机
        if(this.trueVerifyCode === undefined) {
          this.$Message.error("请获取验证码！");
          return;
        }
        if(this.verifyCode !== this.trueVerifyCode) {
          this.$Message.error("验证码错误！");
          return;
        }
        this.verifyCodeLoading = false;
        this.verifyCode = undefined;
        this.trueVerifyCode = undefined;
        this.userInfoStatus = 'editPassword2';
      },
      pwStepTwo: function() {
        // 验证新密码 newPassword confirmNewPassword
        if(this.newPassword === null || this.newPassword === undefined || this.newPassword.length === 0) {
          this.$Message.error("请输入新密码！");
          return;
        }
        if(this.newPassword.length < 6) {
          this.$Message.error("密码长度不能小于6位！");
          return;
        }
        if(this.confirmNewPassword === null || this.confirmNewPassword === undefined || this.confirmNewPassword.length === 0) {
          this.$Message.error("请确认密码！");
          return;
        }
        if(this.newPassword !== this.confirmNewPassword) {
          this.$Message.error("两次密码不一致！");
          return;
        }
        this.$http.post(server.api.user.resetPwd, {
          tel: this.oldTel,
          newPassword: this.newPassword
        }).then((response) => {
          this.$Message.success("修改用户密码成功！需要重新登录");
          setTimeout(() => {
            this.logout();
          }, 1000);
        }, (error) => {
          this.$Message.error("修改密码失败！");
        })
      },
      uploadImg (e) {
        //上传图片
        // this.option.img
        let file = e.target.files[0];
        if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
          this.$Message.error("不支持的图片类型！");
//          alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
          return false
        }
        if(file.size > 5*1024*1024) {
          this.$Message.error("上传图片不能大于5M！");
          return;
        }
        this.imgType = file.type;
        this.imgName = file.name;
        let reader = new FileReader();
        reader.onload = (e) => {
          this.imgFile = e.target.result;
          this.editAvatarModal = true;
          Vue.nextTick(() => {
            this.initCropper();
          });
        };
        reader.readAsDataURL(file);
//        Vue.nextTick(() => {
//          this.initCropper();
//        })
      },
      initCropper: function() {
        $("#edit_avatar_image").cropper('destroy');
        Vue.nextTick(() => {
          $("#edit_avatar_image").cropper({
            aspectRatio: 1/1,
            viewMode: 1,
            dragMode: 'move',
//            aspectRatio: this.aspectRatio,
            autoCropArea: 1,
            restore: false,
            guides: false,
            highlight: true,
            cropBoxMovable: false,
            cropBoxResizable: false,
            modal: false,
          });
        });
      },
      saveAvatarImg: function() {
        // 计算图片大小，判断图片应压缩的比例
        // 0-1M，压缩0.5     800k->44k
        // 1-2M，压缩0.35
        // 2-8M，压缩0.2     2M->176K  5M->238K
        // 8-*M，压缩0.1
        let str = $("#edit_avatar_image").cropper("getCroppedCanvas").toDataURL(this.imgType, 1);
        let fileSize =(str.length - (str.length / 8) * 2) / 1024 / 1024;
        let scale;
        if(fileSize <= 1) {
          scale = 0.5;
        } else if(fileSize <= 2) {
          scale = 0.35;
        } else if(fileSize <=8) {
          scale = 0.2;
        } else {
          scale = 0.1;
        }
        console.log("缩放图片比例：" + scale);
        let img = $("#edit_avatar_image").cropper("getCroppedCanvas").toDataURL(this.imgType, scale);
        // 将base64转换为blob数据
        let blobImg = this.convertBase64UrlToBlob(img);
        let formData = new FormData();
        let name = this.imgName;
        formData.append('file', blobImg, name);
        this.$http.post(server.api.user.avatar.replace('{uid}', localStorage.access_user_id), formData).then((response) => {
          this.editAvatarModal = false;
          this.$Message.success("修改头像成功");
          this.user.avatar = null;
          Vue.nextTick(() => {
            // 加载头像
            this.user.avatar = server.api.avatarImg.replace('{imageId}', response.body.data).replace('{scaleSize}', 'small');
            let userAvatarImg = new Image(80, 80);
            userAvatarImg.onload = function() {
              document.getElementById('div_user_avatar').innerHTML = '';
              document.getElementById('div_user_avatar').appendChild(userAvatarImg);
            };
            userAvatarImg.src = this.user.avatar;
          });
        }, (error) => {
          this.$Message.error("上传图片失败!");
        });
      },
      convertBase64UrlToBlob: function(urlData){
        let bytes=window.atob(urlData.split(',')[1]);        //去掉url的头，并转换为byte
        //处理异常,将ascii码小于0的转换为大于0
        let ab = new ArrayBuffer(bytes.length);
        let ia = new Uint8Array(ab);
        for (let i = 0; i < bytes.length; i++) {          ia[i] = bytes.charCodeAt(i);      }
        return new Blob( [ab] , {type : this.imgType});
      }
    },
    mounted() {
      this.common = common;
      // 登录成功后从localStorage中获取用户信息，暂时注释掉
      this.user = this.$store.state.user;//JSON.parse(localStorage.user);
      // 加载头像
      let userAvatarImg = new Image(80, 80);
      userAvatarImg.onload = function() {
        document.getElementById('div_user_avatar').appendChild(userAvatarImg);
      };
      userAvatarImg.src = this.user.avatar;
    }

  }
</script>



